.work{
    height: 70vh;
    display: flex;
    position: relative;
    margin-top: 5rem;
}
.Clients{
    flex: 1;
    display: flex;
    flex-direction: column;
}
.Clients>:nth-child(1){
    font-size: 2rem;
    font-style: bold;
    color: black;
}
.Clients>:nth-child(2){
    font-size: 3rem;
    font-style: bold;
    color: orange;
    border: black;
}
.Clients>:nth-child(3){
    font-size: 0.81rem;
    color: rgb(108, 99, 99);
    margin-top: 8px;
    margin-bottom: 10px;
}
.w-right{
    position: relative;
    flex: 1;
}
.w-mainCircle{
    position: relative;
    left: 10rem;
    width: 15rem;
    height: 15rem;
    border-radius: 100%;
    background-color: white;
    box-shadow: 0px 10px 10px 2px  black;
}
.w-seccircle{
   
    position: absolute;
    
}
.w-seccircle img{
    width: 80px;
    height: 80px;
    border-radius: 100%;
    border: 4px solid rgb(218, 217, 217);
}
.Yellocircle{
    position: absolute;
    background: rgb(6, 209, 128);
    height: 13rem;
    width: 14rem;
    z-index: -2;
    top: -6%;
    left: 58%;
    border-radius: 100%;
}
.Bluecircle{
    position: absolute;
    background: rgb(248, 224, 9);
    height: 14rem;
    width: 14rem;
    z-index: -2;
    top: 10%;
    left: 60%;
    border-radius: 100%;
}