.ProjectsSwiper {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 70vh;
    justify-content: center;
}

.ProjectsSwiper span:nth-child(1) {
    color: black;
    font-size: 2rem;
    font-weight: bold;
}

.ProjectsSwiper span:nth-child(2) {
    color: orange;
    font-size: 2rem;
    font-weight: bold;
    font-family: sans-serif;
}

.ProjectsSwiper .Swiper {
    width: 100%; /* Ensure the Swiper container is full width */
    overflow: visible !important; /* Fix the overflow issue */
}

.ProjectsSwiper-slider {
    margin-top: 2rem;
    width: 100%;
}

.ProjectsSwiper-slider .SwiperSlide {
   width: 20rem;
}

.ProjectsSwiper img {
    width: 100%; /* Make the image responsive */
    max-width: 19rem; /* Prevent it from exceeding a certain size */
    height: auto; /* Maintain aspect ratio */
    filter: drop-shadow(-12px 15px 13px black);
    border-radius: 20px;
}
