.Services{
    display: flex;
    height: 80vh;
    margin-top: 3rem;
}
.review{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    flex: 1;
}
.review>:nth-child(1){
    font-size: 3rem;
    font-style: bold;
    color: black;
}
.review>:nth-child(2){
    font-style: bold;
    color: rgb(108, 99, 99);
}
.review>:nth-child(3){
    width: 10rem;
    height: 3rem;
}
.cards{
    flex: 1;
    position: relative;
}
.cards>*{
    position: absolute;
}