.contact-form{
    height: 100vh;
    display: flex;

}
.c-left{
    display: flex;
    flex-direction: column;
    position: relative; 
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 2rem;
    flex: 1;
    margin-top: 2rem;
}
.c-left > :nth-child(1) {
    font-size: 2rem;
    font-weight: bolder; 

  }
.c-left > :nth-child(2) {
    font-size: 2.5rem;
    font-weight: bolder; 
    color: orange;
}
.c-right{
    display: flex;
    flex: 1;
    align-items: center;   
    justify-content: center;
    margin-top: 8rem;
    position: relative;
}
form {
    display: flex;           
    flex-direction: column;   
    gap: 3rem;                
    width: 300px;             
}

input.user {
    padding: 10px;
    margin-bottom: 1rem;
    width: 20rem;              
    border-radius: 4px;
    outline: none;
    border: 3px solid orange;
}
textarea.user {
    height: 5rem;
    width: 20rem;
    outline: none;
    border: 3px solid orange;
    border-radius: 4px;
}
  
.thank-you-message{
  position: relative;
  left:-60%;
  display: block;
  margin-top: 10px;
  font-size: 1.2rem;
  font-weight: bold;
  color: #026205;
  text-align: center;
  padding: 10px;
  background: transparent;
  border-radius: 5px;
  box-shadow: 0px 4px 10px #026205;;
}