.expirience{
    height: 65vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: auto;
}
.Achievements{
    display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.circle{
    position: relative; 
    width: 4rem; 
    height: 4rem;  
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    text-align: center;
    background-color: white;
    border-radius: 50%;
    margin: 2rem;
} 
.circle::before {
    content: '';  
    position: absolute; 
    top: -8px;
    left: -8px;
    right: -8px;
    bottom: -8px;
    border-radius: 50%;  
    background-image: linear-gradient(to bottom, #afe630 0%, #feb47b 100%);  
    z-index: -1; 
  }