.nav-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;
}
.nav-left{
    display: flex;
    flex: 1;
    gap: 2rem;
    align-items: center;
}
.n-name{
    font-size: 2rem;
    font-weight: bold;
}
.nav-right{
    display: flex;
    flex: 1;
    gap: 2rem;
    align-items: center;
    font-weight: 400;
    cursor:pointer;
}
.nav-list{
    display: flex;
    gap: 2rem;
    
}
.nav-list > li:hover{
    cursor: pointer;
    color: chocolate;
}