.footer{
    display: flex;
    align-items: center;
    margin: -0.5rem -3.5rem;
    position: relative;
    align-items: center;
    justify-content: center;
    margin-top: -4rem;
}
.f-content{
    align-items: center;
    justify-content: center;
    position: absolute;
    display: flex;
    flex-direction: column;
    color: white;
    gap: 3rem;
    margin-top: 5rem;
}
.f-content > span{
    align-items: center;
    justify-content: center;
    display: flex;

}
.f-container{
    align-items: center;
    justify-content: center;
    display: flex;
    gap: 2rem;
}